import { useContext } from 'react';
import { FiSearch } from 'react-icons/fi';
import ProjectSpotlight from './ProjectSpotlight';
import { ProjectsContext } from '../../context/ProjectsContext';
import ProjectsFilter from './ProjectsFilter';
import { useTranslation } from 'react-i18next'

const ProjectsGrid = (props) => {
	const {
		projects,
		searchProject,
		projectCategories,
		setSearchProject,
		searchProjectsByTitle,
		selectProject,
		setSelectProject,
		selectProjectsByCategory,
		spotlightProject,
		setSpotlightProject
	} = useContext(ProjectsContext);
	const { t } = useTranslation();

	return (
		<section className="py-5 sm:py-10 mt-5 sm:mt-10">
			<div className="text-center">
				<p className="font-general-medium text-2xl sm:text-4xl mb-1 text-ternary-dark dark:text-ternary-light">
					{t('Projects portfolio')}
				</p>
			</div>

			<div className="mt-10 sm:mt-16">
				<h3
					className="font-general-regular 
                        text-center text-secondary-dark
                        dark:text-ternary-light
                        text-md
                        sm:text-xl
                        mb-3
                        "
				>
					{t('Search projects by title or filter by category')}
				</h3>
				<div
					className="
                        flex
                        justify-between
                        border-b border-primary-light
                        dark:border-secondary-dark
                        pb-3
                        gap-3
                        "
				>
					<div className="flex justify-between gap-2">
						<span
							className="
                                hidden
                                sm:block
                                bg-primary-light
                                dark:bg-ternary-dark
                                p-2.5
                                shadow-sm
                                rounded-xl
                                cursor-pointer
                                "
						>
							<FiSearch className="text-ternary-dark dark:text-ternary-light w-5 h-5"></FiSearch>
						</span>
						<input
							onChange={(e) => {
								setSearchProject(e.target.value);
							}}
							className="font-general-medium 
                                pl-3
                                pr-1
                                sm:px-4
                                py-2
                                border 
                            border-gray-200
                                dark:border-secondary-dark
                                rounded-lg
                                text-sm
                                sm:text-md
                                bg-secondary-light
                                dark:bg-ternary-dark
                                text-primary-dark
                                dark:text-ternary-light
                                "
							id="name"
							name="name"
							type="search"
							required=""
							placeholder={t('Search Projects')}
							aria-label="Name"
						/>
					</div>

					<ProjectsFilter categories={projectCategories} setSelectProject={setSelectProject} />
				</div>
			</div>

			<div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 mt-6 sm:gap-10">	
				{selectProject
					? (props.showAll === "true" ? selectProjectsByCategory : selectProjectsByCategory.slice(0,6)).map((project) => (
							<ProjectSpotlight
								title={project.title}
								client={project.ProjectInfo.ClientName}
								image={project.img}
								id={project.id}
								currentSpotlight={spotlightProject}
								key={project.id}
							/>
					  ))
					: searchProject
					? (props.showAll === "true" ? searchProjectsByTitle : searchProjectsByTitle.slice(0,6)).map((project) => (
							<ProjectSpotlight
								title={project.title}
								client={project.ProjectInfo.ClientName}
								image={project.img}
								id={project.id}
								currentSpotlight={spotlightProject}
								key={project.id}
							/>
					  ))
					: (props.showAll === "true" ? projects : projects.slice(0,6)).map((project) => (
							<ProjectSpotlight
								title={project.title}
								client={project.ProjectInfo.ClientName}
								image={project.img}
								id={project.id}
								currentSpotlight={spotlightProject}
								setSpotlightProject={setSpotlightProject}
								key={project.id}
							/>
					  ))}
			</div>
		</section>
	);
};

export default ProjectsGrid;
